@keyframes arrow-animate-1 {
  0% { transform: scale(0) translateY(5px) translateX(-5px) rotate(22.5deg); }
  33% { transform: scale(1) translateY(0px) translateX(0px); opacity: 1; }
  100% { transform: scale(1) translateY(0px) translateX(0px); opacity: 1; }
}

@keyframes arrow-animate-2 {
  0% { transform: scale(0) translateY(5px) translateX(-5px) rotate(22.5deg); }
  33% { transform: scale(1) translateY(0px) translateX(0px); opacity: 1; }
  66% { transform: scale(1) translateY(0px) translateX(0px); opacity: 1; }
  67% { opacity: 0; }
  100% { opacity: 0; }  
}

@keyframes arrow-animate-3 {
  0% { transform: scale(0) translateY(5px) translateX(-5px) rotate(22.5deg); }
  33% { transform: scale(1) translateY(0px) translateX(0px); opacity: 1; }
  34% { opacity: 0; }
  100% { opacity: 0; }  
}

@keyframes celestial-animate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}